export async function getStaticProps({query}) {
  const res = await fetch(`http://admin.carnemag.co:1337/artists?name=${query.name}&lastname=${query.lastname}`)
  const artist = await res.json()

  return {
    artist: artist[0]
  }
}

export default Artist